import { ExamsService } from 'src/app/services/exams.service';
import { GrupoExamesService } from './../../services/grupo-exames.service';
import { NavigationExtras, Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-menu-exames',
  templateUrl: './menu-exames.page.html',
  styleUrls: ['./menu-exames.page.scss'],
})
export class MenuExamesPage implements OnInit {
  gruposExames;
  constructor(
    private router: Router,
    private gpExSrv: GrupoExamesService,
    private exaSrv: ExamsService
    ) { }

  ngOnInit() {
    this.loadGroupExams()
  }

  loadGroupExams(){
    this.gpExSrv.getGroupExams().subscribe((data)=>{
      this.gruposExames = data
    })
    this.exaSrv.getExames().subscribe((data)=>{
      console.log(data)
    })
  }

  goToMealPlaner(id: number){
    let navigationExtras: NavigationExtras = {
      queryParams: {
        grupo_id: id
      }
    }
    this.router.navigate(['meal-planner'],navigationExtras)
  }
  goToPerfilHormonal(){
    this.router.navigate(['perfil-hormonal'])
  }
  goToPerfilPerformace(){
    this.router.navigate(['perfil-peformance'])
  }
  goToPerfilLipidico(){
    this.router.navigate(['perfil-lipidico'])
  }
  goToFuncaoHepatica(){
    this.router.navigate(['funcao-hepatica'])
  }
  goToFuncaoRenal(){
    this.router.navigate(['funcao-renal'])
  }
  goToVitaminas(){
    this.router.navigate(['vitaminas'])
  }
  backToBegin(){
    this.router.navigate(['tabs/tab1'])
  }

}
